// ** User List Component
import Table from './Table'

// ** Reactstrap Imports
import { Row, Col } from 'reactstrap'

// ** Custom Components
import StatsHorizontal from '@components/widgets/stats/StatsHorizontal'

// ** Icons Imports
import { Plus} from 'react-feather'

// ** Styles
import '@styles/react/apps/app-users.scss'
import { useSelector } from 'react-redux'

const UsersList = () => {
  const store = useSelector(state => state.delivery)
  const codes = store.deliveryCodes
  const active = codes.filter(employee => employee.status === true)
  const inActive = codes.length - active.length
  return (
    <div className='app-user-list'>
      <Row>
      <Col lg='4' sm='6'>
          <StatsHorizontal
            color='primary'
            statTitle='Total Delivery Codes'
            icon={<Plus  size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{codes.length}</h3>}
          />
        </Col>
        <Col lg='4' sm='6'>
          <StatsHorizontal
            color='success'
            statTitle='Active Delivery Codes'
            icon={<Plus size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{active.length}</h3>}
          />
        </Col>
        <Col lg='4' sm='6'>
          <StatsHorizontal
            color='danger'
            statTitle='Inactive Delivery Codes'
            icon={<Plus size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{inActive}</h3>}
          />
        </Col>
      </Row>
      <Table />
    </div>
  )
}

export default UsersList
